//
// Remove underline from all links
//
a:link,
a:visited,
a:hover,
a:active {
    text-decoration: none;
}

//
// Ensure main page is rendered to full viewport height
//
.main {
    min-height: 100vh;
}

//
// Table of contents sidebar
//
.toc {
    grid-area: toc;
    top: 5rem;
    right: 0;
    z-index: 2;
    height: calc(100vh - 7rem);
    overflow-y: auto;
}

.toc nav {
    font-size: 0.875rem;
}

.toc nav ul {
    padding-left: 0;
    list-style: none;
}

.toc nav ul ul {
    padding-left: 1rem;
    margin-top: 0.25rem;
}

.toc nav li {
    margin-bottom: 0.25rem;
}

.toc nav a {
    color: inherit;
}

.toc nav a:not(:hover) {
    text-decoration: none;
}

.toc nav a code {
    font: inherit;
}
